.header {
   background-color: #fff;
}
.burger {
   display: none;
   @include burger;
}
.header-navigation {
   display: flex;
   align-items: center;
   justify-content: space-between;
   margin-top: 24px;
   padding: 8px 0;
   border-bottom: 1px solid #ebebfb;
   background-color: #fff;
}
.nav {
   background-color: #fff;

		&__list {
         display: flex;
		}

		&__item {
         margin-left: 30px;
         &:first-child {
            margin-left: 0;
         }
		}

		&__link {
         font-family: 'Montserrat', sans-serif;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 140%;
		}
}
.contacts {
   display: flex;
   align-items: center;

		&__phone {
         font-family: 'Montserrat', sans-serif;
         font-style: normal;
         font-weight: 500;
         font-size: 18px;
         line-height: 140%;
         margin-right: 30px;
		}
      &__button {
         display: flex;
         min-width: 180px;
      }
		&__btn {
         font-size: 16px;
         line-height: 140%;
         color: #fff;
         background-color: #3e3e48;
         border: 1px solid #3e3e48;
         padding: 17px 20px;
         cursor: pointer;
         transition: .2s ease 0s;
         &:hover {
            background-color: #fff;
            color: #3e3e48;

         }
		}
}
