// базовые подключения
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";

img {
   display: block;
}
// main-img//
.main-title {
   display: flex;
   justify-content: center;
   align-items: center;
   &__title {
      font-family: 'PlayfairDisplay-Regular', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: calc(38px + 106 * (100vw / 1200));
      line-height: 140%;
      text-align: center;
      letter-spacing: 0.35em;
      text-transform: uppercase;
      color: #3E3E48;
   }
}
.main-img img{
   width: 100%;
   object-fit: cover;
   min-width: 50%;
   min-height: 325px;
}
//Company//
.section {
   margin: 200px 0 0;
   overflow: hidden;
}
.content-about {
   width: 100%;
   display: flex;
   justify-content: space-between;

		&__body {
         box-sizing: border-box;
         width: 100%;
         max-width: 900px;
         min-width: 0;
		}
      &__body-container {
         display: flex;
      }
		&__body-content {
         display: flex;
         width: 100%;
         max-width: 420px;
         font-family: 'Manrope-Regular', sans-serif;
         font-style: normal;
         color: #434343;
         margin-left: 50px;
         &:first-child {
            margin-left: 0;
         }
		}
      &__body-img {
         display: flex;
         justify-content: center;
         max-width: 420px;
         min-height: 340px;

         & img {
            width: 100%;
            height: 100%;
            background-size: cover;
         }
      }
      &__body-img-last {
         display: flex;
         justify-content: center;
         max-width: 100%;
         min-height: 225px;

         & img {
            width: 100%;
            height: 100%;
            background-size: cover;
         }
      }
		&__paragraph {
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
		}
      &__link {
         font-family: 'Montserrat-Medium', sans-serif;
         font-style: normal;
         font-weight: 500;
         font-size: 14px;
         line-height: 140%;
         color: #3e3e3e;
         padding-bottom: 5px;
         position: relative;
         &:after {
            content: "";
            position: absolute;
            bottom: 0px;
            left: 0;
            width: 100%;
            height: 1px;
            background-color: #3e3e3e;
         }
         &:hover {
            color: #7b61ff;
         }
         &:hover:after {
            background-color: #7b61ff;
         }
      }
}
.section-title {
   font-family: 'PlayfairDisplay-Regular', sans-serif;
   font-style: normal;
   letter-spacing: 0.1em;
   text-transform: uppercase;
   min-width: 300px;
   max-width: 300px;

		&__title {
         font-weight: 400;
         font-size: 20px;
         line-height: 140%;
         margin-right: 20px;
		}
}
.about-video {
   margin-top: 100px;
   position: relative;


   &__img {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80px;
      height: 80px;
      border: 1px solid #fff;
      border-radius: 50px;
      cursor: pointer;

      & img {
         padding-left: 5px;
      }
   }
}
.image img{
   width: 100%;
   min-height: 260px;
   object-fit: cover;
}

// Advantages slider//
.slider {
  //width: 1000px;
   overflow: hidden;
   position: relative;
   margin-right: -300px;

   &__wrapper {
      display: flex;
   }
}
.slider-slide {
   &__content-img {
      & img {
         width: 100%;
         height: 100%;
         background-size: cover;
      }
   }
   &__content-text {
      margin-top: 30px;
   }
}
.swiper-button-next, .swiper-rtl .swiper-button-prev {
   right: 0;
   left: auto;
}
.swiper-button-prev, .swiper-rtl .swiper-button-next {
   left: 0;
   right: auto;
}
.swiper-button-prev:after{
   display: none;
}
.arrow.swiper-button-prev:after{
   display: block;
   content: "";
   position: absolute;
   top: 50%;
   left: -10px;
   transform: translate(0, -50%);
   width: 31px;
   height: 8px;
   background: url('../img/slider/arrow-slider.svg') no-repeat center top / cover;
}
.swiper-button-next:after{
   right: 0;
   display: none;
}
.arrow.swiper-button-next:after {
   display: block;
   content: "";
   position: absolute;
   // top: 50%;
   left: 10px;
   transform: translate(0, -50%);
   width: 31px;
   height: 8px;
   background: url('../img/slider/arrow-slider.svg') no-repeat center top / cover;
   transform: rotate(180deg);
}
.content-about__body .slider-nav-panel {
   margin-top: 30px;
   display: flex;
   align-items: center;
   width: 110px;
   height: 40px;
   position: relative;
}
.content-about__body .slider-pagination{
   position: absolute;
   top: 50%;
   left: 50%;
   width: 40px;
   height: 100%;
   transform: translate(-50%, -50%)
}
.swiper-scrollbar {
   max-width: 880px;
   cursor: pointer;
   height: 5px;
   border: 1px solid #efebfb;
   background-color: #fff;
   box-sizing: border-box;
}
.content-about__body .swiper-scrollbar-drag {
   background: #ebebfb;
}
//====Style===============
.upper {
   font-family: 'PlayfairDisplay-Regular', sans-serif;
   font-style: normal;
   font-weight: 400;
   font-size: 20px;
   line-height: 140%;
   letter-spacing: 0.1em;
   text-transform: uppercase;
   color: #3E3E48;
}
.style .content-about__paragraph {
   max-width: 100%;
}
.housing .content-about {
   padding: 50px 0;
   border-top: 1px solid #ebebfb;
   border-bottom: 1px solid #ebebfb;
   &:last-child {
      border-top: none;
   }
   &__body-content {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
   }
}
.floors.content-about {
   padding-bottom: 50px;
   border-bottom: 1px solid #ebebfb;
}
.floors.section {
   margin-bottom: 200px;
}
.cpecification {

   &__item {
      display: flex;
      font-family: 'Manrope-Regular', sans-serif;
      font-style: normal;
      font-weight: 400;
      color: #787878;
      padding: 20px 0;
      border-bottom: 1px solid #ebebfb;

      &:hover {
         background-color: #f8f8ff;
         color: #434343;
      }
   }

   &__item:hover .cpecification__item-img{
      display: flex;
   }

   &__item-title {
      width: 100%;
      font-size: 16px;
      line-height: 140%;
   }

   &__item-content {
      width: 100%;
      font-size: 14px;
      line-height: 140%;
      position: relative;
   }
   &__item-img {
      display: none;
      object-fit: cover;
      position: absolute;
      top: -20px;
      left: 0;
      max-width: 420px;
      height: 260px;
      z-index: 1;
      overflow: hidden;

      & img {
         width: 100%;
         height: 100%;
         background-size: cover;
      }
   }
}

//tabs
.tabs {
   display: flex;
   align-items: center;

}
.tabs-content {
   display: flex;
}
.tabs .tab-title {

		&__title {
         font-family: 'Manrope-Regular',sans-serif;
         font-style: normal;
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: #787878;
         text-transform: none;
         letter-spacing: 0;
         margin-bottom: 50px;
         // padding-left: 0;
         cursor: pointer;
         position: relative;
         &:last-child {
            margin-bottom: 0;
         }
         &:before {
            display: none;
            content: "";
            width: 20px;
            height: 1px;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate (0, -50%);
            background-color: #3e3e3e;
         }
         &:hover.tab-title__title {
            padding-left: 25px;
         }
         &:hover:before {
            display: block;
         }
		}
}
.tab-title__title.active {
   padding-left: 25px;
   &:before {
      display: block;
   }
}
.tabs {
   margin-top: 80px;
}
.tab-content {
   &__content{
      display: none;
      overflow: hidden;
      width: 100%;
      min-height: 450px;
      border: 1px solid orangered;
   }
   &__content.active {
      display: block;
   }
}
.mapsid {
   display: block;
   width: 100%;
   min-height: 260px;
   height: 450px;
}

@import "components/slider-down";
@import "components/footer";
@import "adaptive";
